import {ApolloClient, InMemoryCache, gql, makeVar} from '@apollo/client'

const typeDefs = gql`
  extend type Query {
    hidden: Boolean!
    cartItems: [ID!]!
  }
`
const toggleHiddenVar = makeVar(true)
const cartItemsVar = makeVar([])
const client = new ApolloClient({
  uri: 'https://crwn-clothing.com/',
  fetchOptions: {
    mode: 'no-cors',
  },
  cache: new InMemoryCache({
    typePolicies: {
      Query: {
        fields: {
          hidden: {
            read() {
              return toggleHiddenVar()
            },
          },
          cartItems: {
            read() {
              return cartItemsVar()
            },
          },
        },
      },
    },
  }),
  typeDefs,
})

const GET_HIDDEN = gql`
  query cartHidden {
    hidden @client
  }
`

const GET_CART_ITEMS = gql`
  query cartItems {
    cartItems @client
  }
`
const GET_COLLECTIONS = gql`
  query {
    collections {
      id
      title
      items {
        id
        name
        price
        imageUrl
      }
    }
  }
`
const GET_COLLECTIONS_BY_TITLE = gql`
  query getCollectionsByTitle($title: String!) {
    getCollectionsByTitle(title: $title) {
      id
      title
      items {
        id
        name
        price
        imageUrl
      }
    }
  }
`

export {
  toggleHiddenVar,
  cartItemsVar,
  client,
  GET_CART_ITEMS,
  GET_HIDDEN,
  GET_COLLECTIONS,
  GET_COLLECTIONS_BY_TITLE,
}
